html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-dmSans);
    background-color: var(--pale-orange);
}

h1, h2, h3 {
    margin-top: 0;
}

a, a:visited, a:active {
    text-decoration: none;
}