:root {
  --font-dmSans: "DM Sans", sans-serif;
}

:root {
  --soft-red: hsl(10, 79%, 65%);
  --cyan: hsl(186, 34%, 60%);
  --dark-brown: hsl(25, 47%, 15%);
  --medium-brown: hsl(28, 10%, 53%);
  --cream: hsl(27, 66%, 92%);
  --pale-orange: hsl(33, 100%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-dmSans);
  background-color: var(--pale-orange);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.card {
  margin: 1em 0;
  padding: 2em;
  border-radius: 15px;
}
.card.balance-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: var(--soft-red);
  color: #fff;
  display: grid;
  row-gap: 10px;
  grid-template-areas: "title title title logo" "amount amount amount logo";
}
.card.balance-container .balance__title {
  grid-area: title;
  font-weight: 400;
  font-size: 0.875rem;
}
.card.balance-container .balance__amount {
  grid-area: amount;
  font-weight: 700;
  font-size: 1.5rem;
}
.card.balance-container .balance__logo {
  grid-area: logo;
  align-self: center;
  justify-self: end;
}

.main-container {
  width: 90%;
  max-width: 500px;
  margin: 20% auto;
}