.card {
    margin: 1em 0;
    padding: 2em;
    border-radius: 15px;

    &.balance-container {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        background-color: var(--soft-red);
        color: #fff;
        display: grid;
        row-gap: 10px;
        grid-template-areas:    'title title title logo'
                                'amount amount amount logo';
        
        .balance__title {
            grid-area: title;
            font-weight: 400;
            font-size: .875rem;
        }

        .balance__amount {
            grid-area: amount;
            font-weight: 700;
            font-size: 1.5rem;
        }

        .balance__logo {
            grid-area: logo;
            align-self: center;
            justify-self: end;
        }
                            

    }
}